.profile {
  display: block;
  position: relative;
  width: 300px;
  background-color: #fdfdfd;
  border-radius: 10px;
  margin: 0 auto;
  box-shadow: 0px 2px 2px rgba(152, 165, 181, 0.5);
  margin-top: 30px;
  margin-bottom: 30px;
  padding-top: 20px;
}

.description {
  width: 100%;
  margin: 0 auto;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  background-color: white;
}

.avatar {
  display: block;
  width: 100px;
  margin: 0 auto;
  border-radius: 50%;
  background-color: #796431;
}

.name {
  display: block;
  margin-top: 30px;
  margin-bottom: 20px;
  font-weight: 600;
  text-align: center;
  font-size: 22px;
}

.tag {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
  opacity: 0.4;
  font-size: 14px;
  font-weight: 700;
}

.location {
  margin: 0;
  padding-bottom: 20px;
  text-align: center;
  opacity: 0.4;
  font-size: 14px;
  font-weight: 700;
}

.stats {
  display: flex;
  flex-wrap: nowrap;
  align-items: center;
  background: #f3f6f9;
  border: 1px solid #e5eaf1;
  list-style: none;
  padding-left: 0px;
}

.statsItem {
  width: 100px;
}

.label {
  display: block;
  text-align: center;
  margin-bottom: 10px;
  opacity: 0.4;
  font-size: 14px;
  font-weight: 700;
}

.quantity {
  text-align: center;
  display: block;
  font-size: 16px;
  font-weight: 700;
}

.stats li {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  align-content: center;
  width: 33.333333%;
  height: 80px;
  gap: 0px;
  border-right: 1px solid #e5eaf1;
}

.stats li:last-child {
  border-right: none;
}

.stats li span {
  display: block;
  width: 100%;
  text-align: center;
  color: #2e3f53;
  line-height: 1.2;
}
